(function () {

//////////////////////////////////////////////////////////////////////////////////////
//                                                                                  //
// packages/gfk:server-messages/shared/internals.js                                 //
//                                                                                  //
//////////////////////////////////////////////////////////////////////////////////////
                                                                                    //
/* global Internals:true*/                                                          // 1
                                                                                    // 2
/***                                                                                // 3
 * Internal namespace used for constants and collection instance                    // 4
 *                                                                                  // 5
 * @namespace                                                                       // 6
 * @type {{constants: {MAX_TIMESTAMP_AGE: number}, collection: Mongo.Collection}}   // 7
 */                                                                                 // 8
Internals = {                                                                       // 9
  constants: {                                                                      // 10
    MAX_TIMESTAMP_AGE: 2500                                                         // 11
  },                                                                                // 12
  collection: new Mongo.Collection('servermessages')                                // 13
};                                                                                  // 14
                                                                                    // 15
//////////////////////////////////////////////////////////////////////////////////////

}).call(this);






(function () {

//////////////////////////////////////////////////////////////////////////////////////
//                                                                                  //
// packages/gfk:server-messages/shared/serverMessages.js                            //
//                                                                                  //
//////////////////////////////////////////////////////////////////////////////////////
                                                                                    //
ServerMessages = function (name) {                                                  // 1
  this._name = name || 'default';                                                   // 2
                                                                                    // 3
  if (_.isFunction(this._init)) {                                                   // 4
    //Client/server specific part of constructor                                    // 5
    this._init.apply(this, arguments);                                              // 6
  }                                                                                 // 7
};                                                                                  // 8
                                                                                    // 9
ServerMessages.prototype = {};                                                      // 10
                                                                                    // 11
//////////////////////////////////////////////////////////////////////////////////////

}).call(this);






(function () {

//////////////////////////////////////////////////////////////////////////////////////
//                                                                                  //
// packages/gfk:server-messages/server/serverMessages.js                            //
//                                                                                  //
//////////////////////////////////////////////////////////////////////////////////////
                                                                                    //
_.extend(ServerMessages.prototype, {                                                // 1
  /***                                                                              // 2
   * Notifies the listeners of the given channel.                                   // 3
   * All other arguments are passed on to the listeners                             // 4
   *                                                                                // 5
   * @param channel the channel to notify                                           // 6
   */                                                                               // 7
  notify: function (channel) {                                                      // 8
    var args = [].slice.call(arguments);                                            // 9
    args.splice(0, 1);                                                              // 10
                                                                                    // 11
    this._cleanupOldMessages();                                                     // 12
                                                                                    // 13
    Internals.collection.insert({                                                   // 14
      instanceName: this._name,                                                     // 15
      channel: channel,                                                             // 16
      arguments: args,                                                              // 17
      timestamp: (new Date().getTime())                                             // 18
    });                                                                             // 19
  },                                                                                // 20
  /***                                                                              // 21
   * Cleans up old messages that are expired                                        // 22
   * @private                                                                       // 23
   */                                                                               // 24
  _cleanupOldMessages: function () {                                                // 25
    var timestamp = (new Date().getTime()) - Internals.constants.MAX_TIMESTAMP_AGE; // 26
                                                                                    // 27
    Internals.collection.remove({                                                   // 28
      timestamp: {$lt: timestamp}                                                   // 29
    });                                                                             // 30
  }                                                                                 // 31
});                                                                                 // 32
                                                                                    // 33
//////////////////////////////////////////////////////////////////////////////////////

}).call(this);






(function () {

//////////////////////////////////////////////////////////////////////////////////////
//                                                                                  //
// packages/gfk:server-messages/server/publish.js                                   //
//                                                                                  //
//////////////////////////////////////////////////////////////////////////////////////
                                                                                    //
/* global publishMethods:true*/                                                     // 1
                                                                                    // 2
                                                                                    // 3
/***                                                                                // 4
 * Namespace containing the publishes for the package.                              // 5
 * Only exposed for testing purposes                                                // 6
 *                                                                                  // 7
 * @type {{ServerMessages/publishMessages: Function}}                               // 8
 */                                                                                 // 9
publishMethods = {                                                                  // 10
  /***                                                                              // 11
   * Publishes all the messages for the given instanceName                          // 12
   *                                                                                // 13
   * @param instanceName the name of the instance to publish the messages for       // 14
   * @returns {any|Cursor}                                                          // 15
   */                                                                               // 16
  'ServerMessages/publishMessages': function (instanceName) {                       // 17
    var timestamp = (new Date().getTime()) - Internals.constants.MAX_TIMESTAMP_AGE; // 18
                                                                                    // 19
    return Internals.collection.find({                                              // 20
      instanceName: instanceName,                                                   // 21
      timestamp: {$gt: timestamp}                                                   // 22
    });                                                                             // 23
  }                                                                                 // 24
};                                                                                  // 25
                                                                                    // 26
_.each(publishMethods, function (fn, name) {                                        // 27
  Meteor.publish(name, fn);                                                         // 28
});                                                                                 // 29
                                                                                    // 30
                                                                                    // 31
//////////////////////////////////////////////////////////////////////////////////////

}).call(this);
